:root {
  --cWidth: 90%;
  --fade: 6s;
}

h1 {
  text-align: center;
}

#pic {
  width: var(--cWidth);
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#btn {
  margin-top: 20px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#left {
  position: absolute;
  left: 0px;
  width: 50%;
}

#right {
  position: absolute;
  right: 0px;
  width: 50%;
}

.fade-in {
  animation: fadeIn ease var(--fade);
  -webkit-animation: fadeIn ease var(--fade);
  -moz-animation: fadeIn ease var(--fade);
  -o-animation: fadeIn ease var(--fade);
  -ms-animation: fadeIn ease var(--fade);
}
@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

#container-circles {
  position: absolute;
  left: 50%;
  bottom: 90px
}

#outer-circle {
  left: -37px;
  height: 75px;
  width: 75px;
  background-color: hsla(0, 0%, 100%, .4);
  z-index: 1
}

#inner-circle, #outer-circle {
  position: absolute;
  border-radius: 50%
}

#inner-circle {
  left: 50%;
  top: 38px;
  height: 44px;
  width: 44px;
  background: #fff;
  margin: -22px 0 0 -22px;
  z-index: 2
}

#inner-circle.is-clicked {
  height: 38px;
  width: 38px;
  margin: -19px 0 0 -19px
}

#white-flash.normal {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 1;
  -webkit-transition: opacity .9s ease-out;
  -o-transition: opacity .9s ease-out;
  transition: opacity .9s ease-out
}

#white-flash.do-transition {
  opacity: 0;
  background: #fff
}

#display-error {
  color: #000;
  background-color: #fff
}

.react-html5-camera-photo {
  position: relative;
  text-align: center
}

.react-html5-camera-photo>img, .react-html5-camera-photo>video {
  width: var(--cWidth);
}

.react-html5-camera-photo>.display-error {
  width: var(--cWidth);
  margin: 0 auto
}

.react-html5-camera-photo-fullscreen>img, .react-html5-camera-photo-fullscreen>video {
  width: 100vw;
  height: 100vh
}

.react-html5-camera-photo-fullscreen>video {
  -o-object-fit: fill;
  object-fit: fill
}

.react-html5-camera-photo-fullscreen>.display-error {
  width: 100vw;
  height: 100vh
}